@import url(https://fonts.googleapis.com/css2?family=Indie+Flower&display=swap);
body {
  font-family: "Indie Flower", cursive !important;
}


.loading_spinner {
  z-index: 9999;
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  background-color: white;
}

body {
  background: black
    url(http://fc01.deviantart.net/fs71/f/2012/160/b/9/stars_by_paulinemoss-d52un4e.jpg);
  animation: stars 205s linear alternate;
}

#box {
  background: url(https://cdn.rawgit.com/ManzDev/cursos-assets/gh-pages/css3/earth-2.jpg);
  background-size: cover;
  border: 2px solid #000;
  border-radius: 50%;
  width: 200px;
  height: 200px;
  animation: movimiento 20s linear 0s infinite;
  box-shadow: 0 0 25px RGBA(255, 255, 255, 0.1), -8px -8px 15px #000 inset,
    2px 2px 25px #000 inset, -45px -45px 25px RGBA(0, 0, 0, 0.5) inset,
    25px 25px 45px RGBA(0, 0, 0, 0.45) inset;
  margin: 6em auto;
  transform: rotateX(6deg) rotateY(6deg) rotateZ(6deg);
}

@keyframes movimiento {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: 355px 0;
  }
}

@keyframes stars {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: 0 100%;
  }
}

.Homepage {
  background-color: rgb(224, 224, 255);
}

.HomepageTitle {
  text-align: center;
  display: flex;
  justify-content: center;
}

.WheelGif {
  text-align: center;
}

li:first-child .text {
  background-color: violet;
}
li:nth-child(2) .text {
  background-color: indigo;
}
li:nth-child(3) .text {
  background-color: blue;
}
li:nth-child(4) .text {
  background-color: green;
}
li:nth-child(5) .text {
  background-color: yellow;
}
li:nth-child(6) .text {
  background-color: orange;
}
li:nth-child(7) .text {
  background-color: red;
}
li:nth-child(8) .text {
  background-color: violet;
}
li:nth-child(9) .text {
  background-color: indigo;
}
li:nth-child(10) .text {
  background-color: blue;
}
li:nth-child(11) .text {
  background-color: green;
}
li:nth-child(12) .text {
  background-color: yellow;
}

li:first-child {
  transform: rotate(0deg) skewY(-60deg);
}

li:nth-child(2) {
  transform: rotate(30deg) skewY(-60deg);
}

li:nth-child(3) {
  transform: rotate(60deg) skewY(-60deg);
}
li:nth-child(4) {
  transform: rotate(90deg) skewY(-60deg);
}
li:nth-child(5) {
  transform: rotate(120deg) skewY(-60deg);
}
li:nth-child(6) {
  transform: rotate(150deg) skewY(-60deg);
}
li:nth-child(7) {
  transform: rotate(180deg) skewY(-60deg);
}
li:nth-child(8) {
  transform: rotate(210deg) skewY(-60deg);
}
li:nth-child(9) {
  transform: rotate(2400deg) skewY(-60deg);
}
li:nth-child(10) {
  transform: rotate(270deg) skewY(-60deg);
}
li:nth-child(11) {
  transform: rotate(300deg) skewY(-60deg);
}
li:nth-child(12) {
  transform: rotate(330deg) skewY(-60deg);
}

.circle {
  width: 25em;
  height: 25em;
  border: 1px solid black;
  position: relative;
  padding: 0;
  margin: 1em auto;
  border-radius: 50%;
  list-style: none;
  overflow: hidden;
}

li {
  overflow: hidden;
  position: absolute;
  top: 0;
  right: 0;
  width: 50%;
  height: 50%;
  transform-origin: 0% 100%;
}
.text {
  position: absolute;
  left: -100%;
  width: 200%;
  height: 200%;
  text-align: center;
  display: block;
  transform: skewY(60deg) rotate(15deg);
  padding-top: 20px;
  cursor: pointer;
  font-weight: bold;
  font-size: 18px;
}
.arrow {
  width: 0;
  height: 0;
  border-left: 15px solid transparent;
  border-right: 15px solid transparent;
  border-top: 50px solid black;
  position: absolute;
  left: 50%;
  top: -20px;
  margin-top: 170px;
  z-index: 1;
}

.spin-button {
  position: absolute;
  left: 40%;
  width: 120px;
  height: 40px;
  background-color: blue;
  border-radius: 30px;
  margin-top: 20px;
  cursor: pointer;
  outline: none;
  color: white;
  font-size: 18px;
}

.start-rotate {
  animation: start-rotate 1s linear infinite;
}
@keyframes start-rotate {
  100% {
    transform: rotate(360deg);
  }
}

.stop-rotate {
  animation-play-state: paused;
}

.UsersCards {
  display: flex;
}

.ReviewsCards {
  background-color: rgb(161, 161, 253);
  margin-left: 6%;
  margin-bottom: 2%;
  border-radius: 3%;
  width: 80%;
  display: flex;
  justify-content: space-around;
  flex-direction: column;
}

.Buttons {
  margin-top: 1%;
  margin-left: 10%;
}

.ImagesCity {
  display: flex;
}

.WeatherCard {
  /* margin-left: 6%;
  margin-top: 3%; */
  border: 1px white solid;
  border-radius: 5%;
  padding: 1%;
  width: 200px;
  margin: 30px;
}

.Footer {
  background-color: rgb(0, 0, 192);
  height: 100px;
  display: flex;
}

.ExperiencePage {
  margin-bottom: 15%;
}

.ExperiencePageTitle {
  text-align: center;
}

